import React from 'react';
import './css/Boeken.css';
import {
    MDBCard,
    MDBCardBody,
    MDBCardTitle,
    MDBCardText,
    MDBCardImage,
    MDBBtn
  } from 'mdb-react-ui-kit';


const Boeken = ({ boeken, onBoekClick}) => {
    
    return (        
   <>

<div style={{ display: 'flex', flexWrap: 'wrap', gap: '25px', overflow: 'auto' }}> {/* overflow: auto; eklendi */}

    {boeken.map((boek, index) => (
    <MDBCard style={{ width: '200px' }} key={index}> {/* Kartn sabit bir genilii belirlendi */}

        <MDBCardImage src={`./pictohareket/${boek.path}`} position='top' alt='...' />
        <MDBCardBody>
          <MDBCardTitle>{boek.boek}</MDBCardTitle>
          <MDBCardText>
          <MDBBtn href='#'  onClick={() => onBoekClick(2,index, boek.boekMap)}>Spreken</MDBBtn>
          </MDBCardText>
          <MDBBtn href='#'  onClick={() => onBoekClick(1,index, boek.boekMap)}>Luisteren</MDBBtn>
        </MDBCardBody>
      </MDBCard>
    ))}
</div>
   </>
    );
}
export default Boeken;











