import React, { useRef, useEffect } from 'react';

const AudioPlayer = ({ src }) => {
  const audioRef = useRef(null);


  
  useEffect(() => {
    if (audioRef.current) {
        if (audioRef.current && src) { // Ses dosyas varsa ve src prop'u doluysa

            audioRef.current.load(); // Ses dosyasn ykle
            try {
            audioRef.current.play(); // Ses dosyasn otomatik olarak al  
            } catch (error) {
            }
            
        }
    }
  }, [src]);


//  const playAudio = () => {
//    if (audioRef.current) {
//      audioRef.current.play();
//    }
//  };

  return (
    <>
      <audio ref={audioRef}>
        <source src={src} type="audio/mpeg" />
        Taraycnz ses etiketini desteklemiyor. Ltfen gncel bir tarayc kullann.
      </audio>
    </>
  );
}

export default AudioPlayer;
