// src/App.js

import React, { useState, useEffect } from 'react';
import SentenceDisplay from './SentenceDisplay';
import ShuffleWords from './ShuffleWords';
import SortingArea from './SortingArea';
import ControlPanel from './ControlPanel';
import ResimPanel from './ResimPanel';
import PictoResimEkle from './PictoResimEkle';
import AudioPlayer from './AudioPlayer';
import Boeken from './Boeken';
import TextGame from './TextGame';
import Spreken from './Spreken';







function OefenenPagina({onPreviusMenuClick, lesSentences, lesStatistieken, handleWaar, boeken, boekMap}) {
  function splitSentences(lesSentences) {
    const result = [];
    lesSentences.forEach(sentence => {
        const turkishWords = sentence.turkish.split(' '); // Trke cmleyi kelimelere ayr
        const dutchWords =  sentence.dutch.split(' '); //sentence.dutch.join(' '); // Hollandaca kelimeleri birletir ve cmleyi kelimelere ayr
        result.push({ turkish: turkishWords, dutch: dutchWords});
    });
    return result;
}


  //shuffledDutchWords Kartrlan Hollandaca kelimeler Hollandaca kelimeler alanndaki kelimeler
  // sortedDutchWords Sralama alanndaki kelimeler

  const [boekMapp, setBoekMapp] =useState("./sound/"+boekMap+"/");
  const [currentSentenceIndex, setCurrentSentenceIndex] = useState(0);  // useState hook'u kullanarak bileenin iinde kullanacamz state'leri tanmlyoruz. 
  const [shuffledDutchWords, setShuffledDutchWords] = useState([]);  
  const [sortedDutchWords, setSortedDutchWords] = useState([]);
  const [audioSrc, setAudioSrc] = useState("");
  const [correctControl, setCorrectControl] = useState(false);
  const [controlEinde, setControlEinde] = useState(false);

  
  

 
  useEffect(() => {
    shuffleWords();
    
  }, [currentSentenceIndex]);    //useEffect hook'u kullanarak, bileenin yklendiinde veya currentSentenceIndex deitiinde shuffleWords fonksiyonunu aryoruz.



  useEffect(()=> {
    //setAudioSrc(boekMapp+currentSentenceIndex+'nl.mp3');
    //console.log(boekMapp+currentSentenceIndex+'nl.mp3');

  },[correctControl])

  const shuffleWords = () => {   //Bu fonksiyon, seilen cmleye ait Hollandaca kelimeleri kartrp shuffledDutchWords state'ine atar.
    const shuffledDutchWords = [...lesSentences[currentSentenceIndex].dutch.split(" ")].map((word, index) => ({ word, index })).sort(() => Math.random() - 0.5);
    setShuffledDutchWords(shuffledDutchWords);
    console.log(lesSentences[currentSentenceIndex].turkish);
    handleWordxClick();
    setSortedDutchWords([]);  //Belki bu satra gerekyok. Kontrol edeceim
  };

  function checkAccuracy (word) {
    const sortedSentence = sortedDutchWords.map(item => item.word).join(' ') + " " + word;
    const originalSentence = lesSentences[currentSentenceIndex].dutch;
  
    if (sortedSentence === originalSentence) {  // siralanan metin ile orjinal metin karsilastiriliyor. 
      setCorrectControl(true);                  // eger dogru ise true set ediliyor ve Goed Gedaan aktif hale getiriliyor. 
      handleWordNlClick();
      handleWaar(1,0);
    } else {
     // alert("Yanl!");
    }
  };

  const handleWordClick = (word,index,path) => {  //Bu fonksiyon, Hollandaca kelimeler alanndaki bir kelimeye tklandnda alr. Seilen kelimeyi sortedDutchWords state'ine ekler ve shuffledDutchWords state'inden kaldrr.
    //setAudioSrc(`./sound/${word}.mp3`); // Kelimenin doru ses dosyasnn yolunu belirtin

    setShuffledDutchWords(shuffledDutchWords.filter((item) => item.index != index));
    setSortedDutchWords([...sortedDutchWords, {word, index}]);
    checkAccuracy(word,index); // word eklendikten sonra dogruluk kontrolunu yaptiriyoruz.  
    console.log(path);
    //setAudioSrc(path+word+'.mp3'); // Kelimenin doru ses dosyasnn yolunu belirtin
    
  };

  const handleWordxClick = () => {  //Bu fonksiyon, Hollandaca kelimeler alanndaki bir kelimeye tklandnda alr. Seilen kelimeyi sortedDutchWords state'ine ekler ve shuffledDutchWords state'inden kaldrr.
    //setAudioSrc(`./sound/${word}.mp3`); // Kelimenin doru ses dosyasnn yolunu belirtin
    setAudioSrc(boekMapp+currentSentenceIndex+'tr.mp3'); // Kelimenin doru ses dosyasnn yolunu belirtin
    
  };

  const handleWordNlClick = () => {  //Bu fonksiyon, Hollandaca kelimeler alanndaki bir kelimeye tklandnda alr. Seilen kelimeyi sortedDutchWords state'ine ekler ve shuffledDutchWords state'inden kaldrr.
    //setAudioSrc(`./sound/${word}.mp3`); // Kelimenin doru ses dosyasnn yolunu belirtin
    setAudioSrc(boekMapp+currentSentenceIndex+'nl.mp3'); // Kelimenin doru ses dosyasnn yolunu belirtin
    console.log("sese geldik")
    
  };

  const handleSortedWordClick = (word, index) => {  //Bu fonksiyon, sralama alanndaki bir kelimeye tklandnda alr. Seilen kelimeyi shuffledDutchWords state'ine ekler ve sortedDutchWords state'inden kaldrr.
    setSortedDutchWords(sortedDutchWords.filter((item) => item.index != index));
    setShuffledDutchWords([...shuffledDutchWords, {word, index}]);

  };

  const handleNextSentenceClick = () => { //Bu fonksiyon, "Sonraki Cmle" dmesine tklandnda alr ve currentSentenceIndex state'ini bir artrr, bylece bir sonraki cmleyi gsterir.
    if(lesStatistieken[0].lengte!==currentSentenceIndex + 1 ) {
        setCurrentSentenceIndex(currentSentenceIndex + 1);
       if (correctControl) {setCorrectControl(false);}
       else{
        handleWaar(0,1);
       }
       
     }
     else{
        if (!controlEinde) {setControlEinde(true);}
     };
  };

  const handlePreviusSentenceClick = (onPreviusMenu) => { //Bu fonksiyon, "Sonraki Cmle" dmesine tklandnda alr ve currentSentenceIndex state'ini bir artrr, bylece bir sonraki cmleyi gsterir.
    if(currentSentenceIndex - 1!== -1) {
        setCurrentSentenceIndex(currentSentenceIndex - 1);
       if (correctControl) {setCorrectControl(false);}
     }
     {};
  };
 const handelCorrectControl = () =>{
  lesSentences[currentSentenceIndex].resultaat+=1;
  handleNextSentenceClick();
  setCorrectControl(false);
 };

 function handleControlEinde() {
    setControlEinde(false);
 };
 

//      <SentenceDisplay sentence={sentences[currentSentenceIndex].turkish} />
  return (
    <div className="OefenenPagina" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center',  height: '100vh', textAlign: 'center' }}>
    <div>
      <h1>{boeken}</h1>
      <AudioPlayer src={audioSrc} />
      <PictoResimEkle words={lesSentences[currentSentenceIndex].dutch.split(" ")}  wordsx={lesSentences[currentSentenceIndex].turkish} wordPath={boekMap} onWordClick={handleWordxClick} wordsNl={lesSentences[currentSentenceIndex].dutch} onWordNlClick={handleWordNlClick} />      
      <ShuffleWords words={shuffledDutchWords} onWordClick={handleWordClick} path={"./sound/"} />
      <SortingArea sortedWords={sortedDutchWords} onSortedWordClick={handleSortedWordClick} />
      <ControlPanel onNextSentenceClick={handleNextSentenceClick} onPreviusSentenceClick={handlePreviusSentenceClick} controlClick={handelCorrectControl} correctControl={correctControl} onPreviusMenu={onPreviusMenuClick} controlEinde={controlEinde} handleControlEinde={handleControlEinde} /> 
    </div>
    <h3>Waar: {lesStatistieken[0].waar} / {lesStatistieken[0].lengte} </h3>
    <h3>fout: {lesStatistieken[0].fout} / {lesStatistieken[0].lengte} </h3>
    </div>
  );
}

export default OefenenPagina;
